@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, #ff00cc65 0%, #333399d3 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 40px 15px;
}
.tip{position:fixed;padding:10px 20px;border:1px solid #b3c9ce;border-radius:4px;text-align:center;font:italic 14px/1.3 sans-serif;color:#333;background:#f0f0f0;box-shadow:3px 3px 3px rgba(0,0,0,0.3);text-transform:capitalize;pointer-events:none;transform:translate(-100%, -100%)}

.header{display:flex;justify-content:center}.header-link{width:calc(50%);text-align:center;box-sizing:border-box;font-size:20px;text-transform:uppercase;text-decoration:none;letter-spacing:3px;padding:10px 15px;color:#fff;text-shadow:0 0 10px #fff;transition:.3s;background:#1822467a}.header-link:first-child{border-top-left-radius:10px}.header-link:last-child{border-top-right-radius:10px}.header-link_active{background:#182246;transition:.3s}

